<template>
  <v-list ref="list">
    <v-list-item-group>
      <v-list-item
        v-for="(ingredient, index) in sortedList"
        :key="index"
        @click="changeIngredientState(ingredient)"
        class="item"
        ref="items"
        :data-key="ingredient.name"
      >
        <template v-slot:default>
          <v-list-item-action>
            <v-checkbox :input-value="ingredient.active" readonly></v-checkbox>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>{{ ingredient.name }}</v-list-item-title>
          </v-list-item-content>
        </template>
      </v-list-item>
    </v-list-item-group>
  </v-list>
</template>

<script>
export default {
  name: "CustomList",
  props: {
    listItems: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      beforePositions: {},
      afterPositions: {},
      lock: false
    };
  },
  computed: {
    sortedList() {
      return [...this.listItems].sort((a, b) => {
        if (!a.active && b.active) {
          return -1;
        }

        if (a.active && !b.active) {
          return 1;
        }

        return 0;
      });
    }
  },
  methods: {
    changeIngredientState(ingredient) {
      if (!this.lock) {
        this.lock = true;
        for (let i = 0; i < this.$refs.items.length; i++) {
          this.beforePositions[this.$refs.items[i].$el.dataset.key] = {
            index: i,
            offsetTop: this.$refs.items[i].$el.offsetTop,
            offsetHeight: this.$refs.items[i].$el.offsetHeight
          };
        }

        ingredient.active = !ingredient.active;

        requestAnimationFrame(() => {
          const listHeight = this.$refs.list.$el.offsetHeight;
          
          this.$refs.list.$el.style = `height: ${listHeight}px`;
          for (const item of this.$refs.items) {
             item.$el.style = `position: absolute; width: 100%; top: ${
               this.beforePositions[item.$el.dataset.key].offsetTop
             }px;`;
          }
          requestAnimationFrame(() => {
            let height = 0;
            for (const item of this.$refs.items) {
              item.$el.style = `position: absolute; width: 100%; top: ${height}px;`;
              height += this.beforePositions[item.$el.dataset.key].offsetHeight;
            }
            
            setTimeout(() => {
              for (const item of this.$refs.items) {
                item.$el.style = "";
              }
              this.lock = false;
            }, 500);
          });
          
        });


      }

          }
  }
};
</script>

<style scoped>
.item {
  transition: top 0.5s;
}

::v-deep .v-list-item--active::before {
  opacity: 0;
}
</style>
