<template>
  <v-row justify="center">
    <v-col cols="12">
      <v-row>
        <v-col cols="12" md="4" sm="6" v-for="(recipe, index) in recipes" :key="index">
          <v-card class="mx-auto">
            <v-img :src="imageList[recipe.selectedImage]"></v-img>
            <v-card-title>
              {{ recipe.title }}
            </v-card-title>

            <v-card-subtitle class="subheader-color">
              {{ recipe.shortDescription }}
            </v-card-subtitle>

            <v-card-actions>
              <v-btn icon @click="recipe.show = !recipe.show">
                <v-icon>{{ recipe.show ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
              </v-btn>
            </v-card-actions>

            <v-expand-transition>
              <div v-show="recipe.show">
                <v-divider></v-divider>
                <v-card-text>Ingredients</v-card-text>
                <custom-list :list-items="recipe.ingredientsList"></custom-list>
                <v-card-text>
                  {{ recipe.description }}
                </v-card-text>
              </div>
            </v-expand-transition>
          </v-card>
        </v-col>
      </v-row>
    </v-col>
    <v-col>
      <v-row>
        <v-btn color="cyan" fab large dark fixed bottom right @click="dialog = !dialog"
          ><v-icon>mdi-plus</v-icon></v-btn
        >
      </v-row>
    </v-col>
    <v-dialog v-model="dialog" persistent max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">Add recipe</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-col cols="12">
              <v-text-field label="Title" v-model="recipeTitle"></v-text-field>
              <v-text-field label="Short Description" v-model="recipeDesc"></v-text-field>
              <span> Ingredients</span>
              <v-list tile v-model="ingredientsList">
                <v-list-item v-for="(ingredient, index) in ingredientsList" :key="index">
                  <v-list-item-content>
                    <v-list-item-title>{{ ingredient.name }}</v-list-item-title>
                  </v-list-item-content>
                  <v-btn icon @click="removeIngredient">
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </v-list-item>
              </v-list>

              <v-text-field label="Ingredient" v-model="ingredient">
                <v-icon slot="append" color="green" @click="addIngredient">
                  mdi-plus
                </v-icon>
              </v-text-field>
              <v-textarea
                clearable
                clear-icon="mdi-close-circle"
                label="Instructions"
                v-model="textRecipe"
              ></v-textarea>
              <template>
                <v-card class="mx-auto">
                  <v-container class="pa-1">
                    <v-item-group v-model="selectedImage">
                      <v-row>
                        <v-col v-for="image in imageList" :key="image" class="d-flex child-flex" cols="4">
                          <v-item v-slot="{ active, toggle }">
                            <v-img :src="image" @click="toggle">
                              <v-btn icon dark>
                                <v-icon>
                                  {{ active ? "mdi-checkbox-marked-circle" : "mdi-checkbox-blank-circle-outline" }}
                                  <template v-slot:placeholder>
                                    <v-row class="fill-height ma-0" align="center" justify="center"> </v-row>
                                  </template>
                                </v-icon>
                              </v-btn>
                            </v-img>
                          </v-item>
                        </v-col>
                      </v-row>
                    </v-item-group>
                  </v-container>
                </v-card>
              </template>
            </v-col>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDialog">
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="saveDialog">
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import CustomList from "@/components/CustomList";

export default {
  name: "RecipeList",
  components: {
    "custom-list": CustomList
  },
  data: () => ({
    dialog: false,
    recipes: [
      { 
        selectedImage: 0,
        title: "Some title",
        shortDescription: "Some description",
        description: "Longer description",
        show: false,
        ingredientsList: [
          { name: "ing 1", active: false },
          { name: "ing 2", active: false },
          { name: "ing 3", active: false }
        ]
      },
      { 
        selectedImage: 1,
        title: "Some title",
        shortDescription: "Some description",
        description: "Longer description",
        show: false,
        ingredientsList: [
          { name: "ing 1", active: false },
          { name: "ing 2", active: false },
          { name: "ing 3", active: false }
        ]
      },
      { 
        selectedImage: 3,
        title: "Some title",
        shortDescription: "Some description",
        description: "Longer description",
        show: false,
        ingredientsList: [
          { name: "ing 1", active: false },
          { name: "ing 2", active: false },
          { name: "ing 3", active: false }
        ]
      }

    ],

    ingredientsList: [],
    imageList: [
      require("@/assets/img1.jpg"),
      require("@/assets/img2.jpg"),
      require("@/assets/img3.jpg"),
      require("@/assets/img4.jpg"),
      require("@/assets/img5.jpg"),
      require("@/assets/img6.jpg"),
      require("@/assets/img7.jpg"),
      require("@/assets/img8.jpg"),
      require("@/assets/img9.jpg")
    ],
    selectedImage: "",
    recipeTitle: "",
    recipeDesc: "",
    textRecipe: "",
    ingredient: ""
  }),
  methods: {
    closeDialog() {
      this.dialog = false;
    },
    saveDialog() {
      this.recipes.push({
        title: this.recipeTitle,
        shortDescription: this.recipeDesc,
        ingredientsList: this.ingredientsList,
        description: this.textRecipe,
        show: false,
        selectedImage: this.selectedImage,
      });

      this.recipeTitle = "";
      this.recipeDesc = "";
      this.textRecipe = "";
      this.ingredientsList = [];
      this.dialog = false;
      this.selectedImage = "";
    },
    addIngredient() {
      this.ingredientsList.push({
        name: this.ingredient,
        active: false
      });
      this.ingredient = "";
    },
    removeIngredient() {
      this.ingredientsList.splice(this.index, 1);
    },
    changeIngredientState(ingredient) {
      ingredient.active = !ingredient.active;
    }
  }
};
</script>
<style></style>
